function readWorkbook(workbook) {
  var sheetNames = workbook.SheetNames; // 
  var worksheet = workbook.Sheets[sheetNames[0]]; // sheet
  var csv = XLSX.utils.sheet_to_csv(worksheet);
  document.getElementById('result').innerHTML = csv2table(csv);
}

// csv
function csv2table(csv) {
  var html = '<table>';
  var rows = csv.split('\n');
  rows.pop(); // 
  rows.forEach(function(row, idx) {
    var columns = row.split(',');
    columns.unshift(idx + 1); // 
    if (idx == 0) {
      // 
      html += '<tr>';
      for (var i = 0; i < columns.length; i++) {
        html += '<th>' + (i == 0 ? '' : String.fromCharCode(65 + i - 1)) + '</th>';
      }
      html += '</tr>';
    }
    html += '<tr>';
    columns.forEach(function(column) {
      html += '<td>' + column + '</td>';
    });
    html += '</tr>';
  });
  html += '</table>';
  return html;
}

function table2csv(table) {
  var csv = [];
  let trArr = Array.from(document.querySelector(table).querySelectorAll('tr'));
  trArr.forEach(function(trElem) {
    var temp = [];
    const tdArr = Array.from(trElem.querySelectorAll('th, td'));
    tdArr.forEach(function(tdElem) {
      temp.push(tdElem.innerText.trim());
    });
    temp.shift(); // 
    csv.push(temp.join(','));
  });
  //   csv.shift();
  return csv.join('\n');
}

// csvsheet
function csv2sheet(csv) {
  var sheet = {}; // sheet
  csv = csv.split('\n');
  csv.forEach(function(row, i) {
    row = row.split(',');
    if (i == 0) sheet['!ref'] = 'A1:' + String.fromCharCode(65 + row.length - 1) + (csv.length - 1);
    row.forEach(function(col, j) {
      sheet[String.fromCharCode(65 + j) + (i + 1)] = {v: col};
    });
  });
  return sheet;
}

export function exportExcel(elem, name) {
  var csv = table2csv(elem);
  var sheet = csv2sheet(csv);
  var blob = sheet2blob(sheet);
  openDownloadDialog(blob, name);
}

export function json2Excel(data = [], name) {
  var sheet = XLSX.utils.json_to_sheet(data, {skipHeader: true});
  openDownloadDialog(sheet2blob(sheet), name);
}

export function exportSpecialExcel(data = [], format = [], name) {
  var sheet = XLSX.utils.aoa_to_sheet(data);
  sheet['!merges'] = format;
  openDownloadDialog(sheet2blob(sheet), name);
}
